import React from "react";
import style from "./CreateFunction.less";

import { connect } from "react-redux";
import { ActionCreators } from "redux/actions";
import { bindActionCreators } from "redux";
import Wrapper from "../../components/Wrapper/Wrapper";
import LuaBox from "../../components/LuaBox/LuaBox";
import BigInput from "../../components/BigInput/BigInput";
import Spacer from "../../components/Spacer/Spacer";
import BigButton from "../../components/BigButton/BigButton";
import { compileLua, addFunction } from "../../lib";

import { withRouter } from "react-router";

@connect(
  mapStateToProps,
  mapDispatchToProps
)
class CreateFunction extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      code: "--Code",
      name: "Name"
    };
  }
  updateCode(code) {
    this.setState({ code: code }, () => {
      console.log(this.state);
    });
  }
  send() {
    let pid = this.props.match.params.id;
    compileLua(this.state.code).then(r => {
      if (r.data.status && r.data.status == "ok") {
        console.log("ok");
        let byteCode = r.data.bytecode;
        console.log(byteCode);
        addFunction({
          pid: pid,
          name: this.state.name,
          source: this.state.code,
          byteCode: byteCode
        }).then(() => {
          this.props.history.push("/project/view/" + pid);
        });
      } else {
        console.log("err");
      }
    });
  }
  render() {
    return (
      <Wrapper title="Create a function">
        <BigInput
          label={"Function name"}
          value={this.state.name}
          onChange={s => {
            this.setState({ name: s });
          }}
        />
        <Spacer />
        <LuaBox
          value={this.state.code}
          onChange={s => {
            console.log("trying");
            this.updateCode(s);
          }}
        />
        <Spacer />
        <BigButton
          onClick={() => {
            this.send();
          }}
        >
          Save function
        </BigButton>
      </Wrapper>
    );
  }
}

//Make state available as props
function mapStateToProps(state) {
  return {};
}

//Make actions available as functions in props
function mapDispatchToProps(dispatch) {
  return bindActionCreators(ActionCreators, dispatch);
}

//Connect to navigation, redux and export
export default withRouter(CreateFunction);
